<template lang="pug">
button(type="button" :class="classes" @click="onClick" :style="style") 
  svg
    v-if="left_icon"
  | {{label}}
  svg
    v-if="right_icon"
</template>

<script>
export default {
  name: 'my-button',

  props: {
    label: {
      type: String,
      default: '',
    },
    left_icon: {
      type: String,
      default: '',
    },
    right_icon: {
      type: String,
      default: '',
    },
    theme: {
      type: String,
      default: 'filled',
      validator: function (value) {
        return ['filled', 'outline', 'flat', 'plain'].indexOf(value) !== -1;
      },
    },
    size: {
      type: String,
      default: 'medium',
      validator: function (value) {
        return ['small', 'medium', 'large'].indexOf(value) !== -1;
      },
    },
  },

  computed: {
    classes() {
      return {
        'storybook-button': true,
        [`storybook-button--${this.theme}`]: true,
        [`button-size--${this.size}`]: true,
      };
    },
    style() {
      return {
      };
    },
  },

  methods: {
    onClick() {
      this.$emit('onClick');
    },
  },
};
</script>

